body {
    background: -webkit-gradient(linear, left top, left bottom, from(#61c4e3), to(#ffffff));
    background: -webkit-linear-gradient(#61c4e3, #ffffff);
    background: -o-linear-gradient(#61c4e3, #ffffff);
    background: linear-gradient(#61c4e3, #ffffff);
  }
  .App {
    width: 100vw;
    height: 100vh;
  }
  .App-footer, .btn-footer {
    width: 100vw;
    height: 10vh;
    text-align: center;
  }
  .App-footer a {
    font-size: 13pt;
    font-family: 'Comfortaa', cursive;
    color: #fff;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding-right: 16px;
  }
  footer.page-footer {
    background-color: #EB656A;
  }
  .app-carousel {
    width: 90vw;
    height: 60vh;
    padding-left: 6%;
  }
  .carousel .slide .legend {
    text-align: center;
    color: black;
  }
  @media only screen and (min-width: 450px) {
    .app-carousel img {
      height: 30vh;
      padding-left: 6%;
    }
    
    .App-footer a {
      font-size: 14pt;
      padding-right: 25px;
    }
}


